import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import FormStatus from '@vkontakte/vkui/dist/components/FormStatus/FormStatus';

import point from '../img/point.png';
import './Persik.css';

const osName = platform();

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Продать Point Play
		</PanelHeader>
		<img className="Persik" src={point} alt="Persik The Cat"/>
	
         <Div style={{ color: 'gray' }}>
           Вы можете продать свои Point Play по курсу 0.6 рублей за 10 тысяч поинтов.
            <br/>
            Продать свои поинты Вы можете у администратора.
          </Div>
          <Div>
			<Button before={<Icon28MarketOutline color="#FFFFFF"/>} mode="commerce" size="xl" level="2" href="https://vk.me/weakmafaka">Продать</Button>
		    </Div>
		  <FormStatus header="Внимание" mode="error">
        Актуальные цены можно посмотреть на стене нашего сообщества
      </FormStatus>
	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;