import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import bridge from '@vkontakte/vk-bridge';
import Icon28StoryOutline from '@vkontakte/icons/dist/28/story_outline';

import point from '../img/point.png';
import './Persik.css';

const osName = platform();

const Post = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Рассказать о проекте
		</PanelHeader>

	  Публикация записи
	class Foo extends Component {
this.handleClick = bridge.send("VKWebAppShowWallPostBox", {"message": "В магазине PurCur Shop Вы сможете купить или продать поинты по самому выгодному курсу! vk.me/purcur_shop"})};

render() {

<Div><Button before={<Icon28StoryOutline/>} mode="commerce" size="xl" level="2" onClick={this.handleClick}>Нажми на меня</Button></Div>
}

	</Panel>
);

Post.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Post;